* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Jost", sans-serif;
  color: #F2F3F4;
}

html {
  font-size: 45%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}








.main-nav {
  width: 100%;
  height: 10vh;
  display: grid;
  grid-template-columns: 15rem 1.5fr 1fr 3fr 15rem;
  background: #212F3C  
}
.box a{
  margin-bottom: 5px;
  color: #1ee273;
}

.logo {
  display: grid;
  
  grid-column: 2/3;
  justify-content: start;
  align-items: center;
}

.menu-link {
  grid-column: 4/5;
}

.menu-link ul {
  height: 10rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.social-media {
  grid-column: 4/5;
}

.social-media ul {
  height: 10rem;
  display: grid;
  grid-template-columns: 3fr repeat(3, 1fr);
  align-items: center;
  justify-content: flex-end;
}

.social-media ul li {
  text-align: right;
}

.social-media ul li:first-child {
  grid-column: 2/3;
}



.logo h2 {
  font-size: 3.5rem;
  font-weight: 400;
  color: #f8bb12;
  text-transform: uppercase;
 
}

.logo h2 span {
  font-size: 4.5rem;
}

.menu-link ul li {
  font-size: 2.8rem;
  text-decoration: none;
  position: relative;
}

.menu-link ul li:last-child{
  display: none;
}

.menu-link ul li .link {
  text-transform: capitalize;
  color: #1ee273;
 
  transition: 0.5s;
  position: relative;
}
.about{
  color: rgb(248, 143, 6);
}
.menu-link ul li .link::after {
  content: "";
    width: 0;
    height: 3px;
    background: rgb(248, 143, 6);
    position: absolute;
}
.menu-link ul li .link:hover::after{
  width: 100%;
  left: 0;
  bottom: -3px;
  transition: 0.3s;
}

.social-media ul li {
  font-size: 1.8rem;
}


.social-media .hamburger-menu {
  display: none;
}




.hero-section {
  height: 80rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #2C3E50;
}

.hero-section p {
  font-size: 3rem;
  text-transform: capitalize;
}

.hero-section h1 {
  font-size: 5rem;
  text-transform: uppercase;
  text-align: center;
}


@media (max-width: 1080px) {
  .main-nav {
    height: 8rem;
    grid-template-columns: 2rem 2fr 2rem 3fr 2rem;
  }

  .logo,
  .menu-link ul,
  .social-media ul {
    height: 8rem;
  }
}


@media (max-width: 800px) {
  .main-nav {
    height: 7rem;
    grid-template-columns: 2rem 2fr 3fr 2rem 2rem;
  }
  .box{
      display: none;
  }
  

  .menu-link {
    display: none;
  }

  .logo,
  .social-media ul {
    height: 7rem;
  }

  .mobile-menu-link {
    grid-column: 2/4;
    position: relative;
    z-index: 99;
  }
  

  .mobile-menu-link {
    background: #212F3C  ;
    height: 22rem;
    width: 100%;
    color: #F2F3F4;
    display: grid;
    grid-column: 2/5;
    align-items: center;
    justify-content: center;
    padding-left: 3rem;
    transition-property: hight;
        position: absolute;
    top: 6rem;

   
  }

  .mobile-menu-link ul {
    height: 20rem;
    display: flex;
    justify-content: space-around;
    flex-direction: column;
    align-items: start;
  }

  .mobile-menu-link ul li:first-child {
    transition-delay: 0.2s;
  }


  .social-media {
    grid-row: 1/2;
    grid-column: 3/5;
    justify-items: end;
    align-items: center;
    transition: all 2s linear;
  }

  .social-media .social-media-desktop {
    height: 0;
    display: none;
  }

  .social-media {
    height: 7rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 798px) {
  .main-nav {
    height: 6rem;
    grid-template-columns: 1rem 2fr 1fr 1fr 1rem;
  }
  .menu-link ul li:last-child{
    display: block;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }
}

@media (max-width: 520px) {
  .main-nav {
    height: 6rem;
    grid-template-columns: 1rem 4fr 1fr 1fr 1rem;
  }

  .menu-link ul li:last-child{
    display: block;
  }

  .logo,
  .social-media ul {
    height: 6rem;
  }

  .logo h2 {
    font-size: 2rem;
  }

  .social-media {
    height: 6rem;
    display: flex;
    justify-self: end;
    align-items: center;
  }

  .social-media .hamburger-menu {
    display: block;
    font-size: 2.5rem;
  }

  

  .hero-section h1 {
    font-size: 3.8rem;
  }
}
